@tailwind base;
@tailwind components;
@tailwind utilities;

.component {
  @apply px-[6vw] md:px-0 md:max-w-[640px] lg:max-w-[750px] md:mx-auto;
}

.icon {
  @apply h-[23px] w-[23px] md:h-[25px] md:w-[25px];
}

.prose-styled {
  @apply my-12 md:my-[62px] lg:my-[68px] component leading-6 prose prose-h1:text-[40px] prose-headings:font-bold prose-headings:text-black prose-a:font-normal prose-li:my-1 prose-a:no-underline prose-a:text-[#3366cc] prose-p:my-2 prose-pre:bg-gray-100 prose-pre:text-gray-800 prose-pre:bg-gray-100;
}
code {
  @apply bg-gray-100 rounded px-1.5 py-0.5 font-normal 
}

body {
  margin: 0;
  font-family: ui-sans-serif, -apple-system, BlinkMacSystemFont, "Segoe UI", Helvetica, "Apple Color Emoji", Arial, sans-serif, "Segoe UI Emoji", "Segoe UI Symbol";
  -webkit-font-smoothing: auto;
  -moz-osx-font-smoothing: grayscale;
  font-size: 16px;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
